:root {
  --blur-blur: blur(12.5px);
  --blur-none: none;
  --border-widths-none: 0;
  --border-widths-thin: 0.075rem;
  --border-widths-thick: 0.125rem;
  --fonts-inherit: inherit;
  --fonts-body: Inter, system-ui, Roboto, "Helvetica Neue", Arial;
  --fonts-mono: "iAWriter Mono", Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  --font-sizes-inherit: inherit;
  --font-sizes-xsmall: 0.625rem;
  --font-sizes-small: 0.75rem;
  --font-sizes-normal: 0.875rem;
  --font-sizes-medium: 1rem;
  --font-sizes-large: 1.25rem;
  --font-sizes-xlarge: 1.875rem;
  --font-weights-inherit: inherit;
  --font-weights-normal: 400;
  --font-weights-medium: 500;
  --font-weights-semibold: 600;
  --font-weights-bold: 700;
  --letter-spacings-inherit: inherit;
  --letter-spacings-none: 0;
  --letter-spacings-normal: 0.01em;
  --letter-spacings-wide: 0.03em;
  --line-heights-4: 1rem;
  --line-heights-5: 1.25rem;
  --line-heights-6: 1.5rem;
  --line-heights-7: 1.75rem;
  --line-heights-9: 2.25rem;
  --line-heights-inherit: inherit;
  --opacity-0: 0;
  --opacity-50: 0.5;
  --opacity-80: 0.8;
  --opacity-100: 1;
  --radii-none: 0;
  --radii-xs: 0.25rem;
  --radii-sm: 0.5rem;
  --radii-md: 0.75rem;
  --radii-lg: 1rem;
  --radii-circle: 9999px;
  --space-0: 0;
  --space-1: 0.25rem;
  --space-2: 0.5rem;
  --space-3: 0.75rem;
  --space-4: 1rem;
  --space-5: 1.25rem;
  --space-6: 1.5rem;
  --space-8: 2rem;
  --space-10: 2.5rem;
  --space-12: 3rem;
  --space-14: 3.5rem;
  --space-16: 4rem;
  --space-0_25: 0.0625rem;
  --space-0_5: 0.125rem;
  --space-1_5: 0.375rem;
}
[data-theme="dark"] {
