}* {
  background: unset;
  border: unset;
  color: unset;
  font-family: var(--fonts-body);
  font-stretch: unset;
  font-style: unset;
  font-variant: unset;
  font-weight: unset;
  list-style-type: none;
  margin: 0;
  outline: unset;
  padding: 0;
  text-decoration: unset;
}
*, *::before, *::after {
  box-sizing: border-box;
}
*:focus-visible {
  outline-color: var(--colors-border-focus) !important;
  outline-style: solid;
  outline-offset: calc(var(--border-widths-thick) * -1);
  outline-width: var(--border-widths-thick);
  border-color: transparent;
}
